import _ from 'lodash';
import { normalizeNumber } from './currencyFormatter';

export const applyDiscount = (discount) => (total) => {
  const totalBefore = normalizeNumber(total);
  // not discount
  if (!discount) {
    return {
      totalBefore,
      totalAfter: totalBefore,
      discount: 0
    };
  }

  const percentage = normalizeNumber(_.get(discount, 'percentage', 0));

  if (percentage > 0) {
    discount = _.round((totalBefore * percentage) / 100, 0);

    return {
      totalBefore,
      totalAfter: totalBefore - discount,
      discount
    };
  }

  return {
    totalBefore,
    totalAfter: totalBefore,
    discount: 0
  };
};
