import _ from 'lodash';

const addEqual = (name, item) => `${name}=${item}`;

export const formatImageExt = (uri, opts) => {
  if (!opts || _.isEmpty(opts)) return uri;
  const obj = Object.keys(opts).map((key) => ({ key, value: opts[key] }));
  const data = obj.map((item) => addEqual(item.key, item.value)).join('&');
  if (uri.indexOf('?')) {
    return `${uri}&${data}`;
  }
  return `${uri}?${data}`;
};
